import React from 'react';
import { View, Text, StyleSheet } from 'react-native';
import { colors } from '../utils';
export default function BodyRow({
  columns,
  record,
  style
}) {
  return <View style={[styles.row, style]}>
      {columns.map((itm, idx) => {
      // rowÊ∏≤ÊüìÁöÑÂÜÖÂÆπ
      let val = '';
      if (itm.dataIndex.indexOf('.') > -1) {
        const firstKey = itm.dataIndex.split('.')[0];
        const secondKey = itm.dataIndex.split('.')[1];
        val = record[firstKey][secondKey];
      } else {
        val = record[itm.dataIndex];
      }

      // ÊòØÂê¶ÁúÅÁï•Â§ö‰ΩôÊñáÂ≠ó
      let textEllipsize = itm.ellipsis && itm.ellipsis ? {
        numberOfLines: 1,
        ellipsizeMode: 'tail'
      } : null;
      return <View key={itm.dataIndex} style={[idx === 0 && styles.firstLeftCell, styles.cell, itm.style]}>
            {itm.render ? itm.render(record) : <Text selectable={true} {...textEllipsize} style={styles.content}>
                {val}
              </Text>}
          </View>;
    })}
    </View>;
}
const styles = StyleSheet.create({
  row: {
    flexDirection: 'row',
    justifyContent: 'center',
    alignContent: 'center',
    borderBottomWidth: 1,
    borderColor: colors.colorsPalette.dark70
  },
  cell: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'center',
    borderRightWidth: 1,
    borderColor: colors.colorsPalette.dark70,
    paddingTop: 4,
    paddingBottom: 4,
    backgroundColor: '#fff'
  },
  firstLeftCell: {
    borderLeftWidth: 1,
    borderColor: colors.colorsPalette.dark70
  },
  content: {
    color: '#888888'
  }
});